body {
  background-color: #434343 !important;
  font-family: 'Roboto Mono' !important;
}

.cPanelBox {
  height: 600px;
  width: 425px;
  background-color: #7d7d7f;
  border-style: solid;
  border-width: 2px;
  border-radius: 7px;
}

.screenBox {
  height: 130px;
  width: 295px;
  background-color: #47b2b2;
  color: #434343;
}

.button {
  height: 80px;
  width: 80px;
  background-color: #63636e;
  color: #f3f3f3;
  font-size: 40px;
  border-style: solid;
  border-color: black;
  border-width: 1px;
  border-radius: 7px;
  box-shadow: 0px 2px 2px black;
}

.button:hover {
  cursor: pointer;
}

.button:active {
    background-color: #434343;
}
